import React from 'react'
import { Box, Image, Stack, Avatar } from 'grommet'
import { White } from '../global/colors'

function CompanyTab(params) {
  return (
    <Box fill background='#FFFFFF' gridArea='main' pad='small'>
      <Stack anchor="bottom-left">
        <Box height='medium'>
          <Image
            fit='cover'
            src={params.company.cover_picture}
            margin='medium'
          />
        </Box>
        <Stack anchor="center">
          <Avatar background={White} size='xlarge' style={{ border: '1px solid #fff' }} />
          <Avatar src={params.company.profile_picture} size='large' style={{ border: '2px solid #fff' }} />
        </Stack>
      </Stack>
      <h3>{params.company.elevatorPitch}</h3>
      <p>
        {params.company.info}
      </p>
    </Box>
  );
}

export default CompanyTab;
