import React from 'react'
import { Turquoise } from '../global/colors'

function ExternalLink(params) {
    return (
        <a style={{
            color: Turquoise,
            textDecoration: 'none'
        }} href={params.href} target='_blank' rel="noopener noreferrer">{params.href}</a>
    );
}

export default ExternalLink;
