import React from 'react'

function Currency(params) {
    if (params.currency === 'GBP') {
        return (
            <span>£</span>
        );
    }
    if (params.currency === 'USD') {
        return (
            <span>$</span>
        );
    }
    if (params.currency === 'EUR') {
        return (
            <span>€</span>
        );
    }
    return <span />
}

export default Currency