import React from 'react'

function IconLabelButton(params) {

  return (
    <span className="icon"
      style={{
        pointerEvents: 'initial',
        cursor: 'pointer',

        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        marginLeft: 24,
        marginRigh: 24
      }} onClick={params.onClick} >
        <i
          className={params.icon} style={params.style}
        />
      <span style={params.labelStyle}>
        {params.label}
      </span>
    </span >
  );
}

export default IconLabelButton