import React from 'react'
import { Purple } from './colors'

function Tabs(params) {
  return (
    <div className="tabs" style={{
      marginBottom: 10,
      borderBottom: 'none',
    }}>
      <ul style={{
        borderBottom: 'none',
      }}>
        {params.tabs && params.tabs.map((name, index) => (
          <li className={`${!params.position || params.position === index ? "is-active" : ""}`}
            style={{
              marginRight: 10,
              paddingRight: 20,
              color: (params.position === index ? 'black' : Purple),
              fontWeight: 'bold',
              borderBottom: 'solid 2px ' + (params.position === index ? 'black' : Purple),
              cursor: 'pointer',
            }}>
            <h5 onClick={() => params.setPosition(index)}>{name}</h5>
          </li>
        ))}
      </ul>
    </div>
  )
}

export default Tabs;
