import React from 'react'
import TextInput from './global/TextInput'

function PostJobDescription(params) {
  const setDescription = function (event) {
    if (event.target.value.length <= 500) {
      params.setDescription(event.target.value)
    }
  }
  return (
    <div>
      <h3>Description</h3>
      <div className="control">
        <textarea
          className="textarea has-fixed-size"
          placeholder='Describe a little about the project your company/client is going through. Be concise. You only have 80 words!'
          onChange={setDescription}
        >
          {params.description}
        </textarea>
      </div>
      {params.description.length} / 500
      <h3>Job Responsibilities</h3>
      You can have up to five
      <TextInput
        placeholder='Type job responsibility here'
        onChange={event => params.setResponsibility1(event.target.value)}
        value={params.responsibility1}
      />
      <TextInput
        placeholder='Type job responsibility here'
        onChange={event => params.setResponsibility2(event.target.value)}
        value={params.responsibility2}
      />
      <TextInput
        placeholder='Type job responsibility here'
        onChange={event => params.setResponsibility3(event.target.value)}
        value={params.responsibility3}
      />
      <TextInput
        placeholder='Type job responsibility here'
        onChange={event => params.setResponsibility4(event.target.value)}
        value={params.responsibility4}
      />
      <TextInput
        placeholder='Type job responsibility here'
        onChange={event => params.setResponsibility5(event.target.value)}
        value={params.responsibility5}
      />
      <h3>Required Experience</h3>
      You can have up to five
      <TextInput
        value={params.experience1}
        onChange={event => params.setExperience1(event.target.value)}
        placeholder='Type previous experience here'
      />
      <TextInput
        value={params.experience2}
        onChange={event => params.setExperience2(event.target.value)}
        placeholder='Type previous experience here'
      />
      <TextInput
        value={params.experience3}
        onChange={event => params.setExperience3(event.target.value)}
        placeholder='Type previous experience here'
      />
      <TextInput
        value={params.experience4}
        onChange={event => params.setExperience4(event.target.value)}
        placeholder='Type previous experience here'
      />
      <TextInput
        value={params.experience5}
        onChange={event => params.setExperience5(event.target.value)}
        placeholder='Type previous experience here'
      />
    </div>
  )
}

export default PostJobDescription;