import React from 'react'
import Box from '../global/Box'

import { Turquoise } from '../global/colors'

function Job(params) {
  return (
    <Box>
      {params.description}
      <h5 style={{ marginBottom: 8 }}>Job Responsibilities:</h5>
      {params.responsibilities.map(text => (
        <div>
          {text !== '' &&
            <div>
              <i color={Turquoise} className='fas fa-check-circle'></i>
              <span style={{ marginLeft: 4, lineHeight: 1 }}>{text}</span>
            </div>
          }
        </div>
      ))}
      <h5 style={{ marginBottom: 8 }}>Required Experience:</h5>
      {params.experiences.map(text => (
        <div>
          {text !== '' &&
            <div>
              <i color={Turquoise} className='fas fa-check-circle'></i>
              <span style={{ marginLeft: 4, lineHeight: 1 }}>{text}</span>
            </div>
          }
        </div>
      ))}
      {params.businessTravel && params.businessTravel !== '0' &&
        <h3> Business Travel: {params.businessTravel}%</h3>
      }
    </Box>
  );
}

export default Job;
