import React from 'react'

function RadioInput(params) {
  return (
    <div className="control">
      {params.options && params.options.map(option => (
        <label className="radio">
          <input
            checked={option.value === params.value ? "checked" : ""}
            type="radio"
            name={params.name}
            value={option.value}
            onClick={event => params.onChange(event.target.value)}
          />
          {option.label}
        </label>
      ))}
    </div >
  );
}

export default RadioInput;